<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="sort">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small" @click="editService(false, null)">新增分类服务</el-button>
            </div>
            <div>
                <el-input
                    placeholder="请输入服务分类名称"
                    size="small"
                    style="width: 200px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="请选择状态"
                           style="width: 140px">
                    <el-option
                        v-for="(v,k) in $Cfg.postType"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="">查询</el-button>
            </div>
        </ToolBar>
        <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
                prop="ID"
                label="ID"
                width="180">
            </el-table-column>
            <el-table-column
                prop="serviceName"
                label="服务分类名称">
            </el-table-column>
            <el-table-column
                prop="status"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="ID"
                label="最近操作人"
                width="180">
            </el-table-column>
            <el-table-column
                prop="date"
                label="创建时间"
                width="180">
            </el-table-column>
            <el-table-column
                label="操作"
                width="210">
                <template slot-scope="scope">
                    <el-button @click="enable(scope.row)" v-if="scope.row.isOpen" type="success" size="small"
                    >启用
                    </el-button>
                    <el-button @click="forbidden(scope.row)" v-else type="default" size="small"
                    >禁用
                    </el-button>
                    <el-button @click="editService(true, scope.row)" type="primary" size="small"
                    >修改
                    </el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                    >删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import G from 'lodash/get'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    isOpen: true,
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: true,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    isOpen: false,
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: false,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            editService(isEdit,row){

                this.$prompt('请输入服务分类名称', isEdit ? '修改服务分类':'新增服务分类', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue:isEdit ? G(row,'b','') : '',
                    inputValidator: v=>{return (v||'').length <=32 && (v||'').length >=1},
                    inputErrorMessage: '不能为空或不能超过8位字符'
                }).then(({ value }) => {
                    this.$message({
                        type: 'success',
                        message: '你的邮箱是: ' + value
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'
                    });
                });
            },
            forbidden(row) {
                this.$confirm('确定禁用该课程包?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该课程包?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用'
                    });
                });
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
